import React, { useState } from "react";
import { components } from "../catalog/components";
import {
  H1,
  H2,
  H3,
  P,
  Section,
  Article,
  Form,
  Fieldset,
  Legend,
  Input,
  Textarea,
  Button,
  Select,
  Option,
  Label,
  Progress,
  Meter,
  Output,
  DataList,
  Optgroup,
  H4,
} from "tanuki-ui";

const FormCatalog: React.FC = () => {
  // 
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    age: 25,
    bio: "",
    country: "",
    interests: [] as string[],
    newsletter: false,
    rating: 5,
    favoriteColor: "#000000",
  });

  const [submitResult, setSubmitResult] = useState("");

  // 
  const updateField = (field: string, value: any) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
  };

  // 
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setSubmitResult(JSON.stringify(formData, null, 2));
  };

  // 
  const formComponents = [...(components.form?.components || []), ...(components["form-additional"]?.components || [])];

  // 
  const renderInteractiveForm = () => (
    <Form onSubmit={handleSubmit}>
      <Fieldset>
        <Legend></Legend>

        <Section>
          <Label htmlFor="name"> *</Label>
          <Input
            id="name"
            type="text"
            value={formData.name}
            onChange={(e) => updateField("name", e.target.value)}
            placeholder=" "
            required
          />
        </Section>

        <Section>
          <Label htmlFor="email"> *</Label>
          <Input
            id="email"
            type="email"
            value={formData.email}
            onChange={(e) => updateField("email", e.target.value)}
            placeholder="example@email.com"
            required
          />
        </Section>

        <Section>
          <Label htmlFor="age">: {formData.age}</Label>
          <Input
            id="age"
            type="range"
            min="18"
            max="100"
            value={formData.age}
            onChange={(e) => updateField("age", parseInt(e.target.value))}
          />
        </Section>
      </Fieldset>

      <Fieldset>
        <Legend></Legend>

        <Section>
          <Label htmlFor="country"></Label>
          <Select id="country" value={formData.country} onChange={(e) => updateField("country", e.target.value)}>
            <Option value=""></Option>
            <Optgroup label="">
              <Option value="japan"></Option>
              <Option value="korea"></Option>
              <Option value="china"></Option>
            </Optgroup>
            <Optgroup label="">
              <Option value="usa"></Option>
              <Option value="canada"></Option>
            </Optgroup>
            <Optgroup label="">
              <Option value="uk"></Option>
              <Option value="france"></Option>
              <Option value="germany"></Option>
            </Optgroup>
          </Select>
        </Section>

        <Section>
          <Label htmlFor="bio"></Label>
          <Textarea
            id="bio"
            value={formData.bio}
            onChange={(e) => updateField("bio", e.target.value)}
            placeholder="..."
            rows={4}
          />
        </Section>

        <Section>
          <Label htmlFor="favoriteColor"></Label>
          <Input
            id="favoriteColor"
            type="color"
            value={formData.favoriteColor}
            onChange={(e) => updateField("favoriteColor", e.target.value)}
          />
        </Section>
      </Fieldset>

      <Fieldset>
        <Legend></Legend>

        <Section>
          <Label htmlFor="rating"> (1-10)</Label>
          <Input
            id="rating"
            type="number"
            min="1"
            max="10"
            value={formData.rating}
            onChange={(e) => updateField("rating", parseInt(e.target.value))}
          />
          <Output htmlFor="rating">: {formData.rating}/10</Output>
        </Section>

        <Section>
          <Progress value={formData.rating} max="10">
            : {formData.rating}/10
          </Progress>
        </Section>

        <Section>
          <Meter value={formData.rating} min={1} max={10} optimum={8}>
            : {formData.rating}/10
          </Meter>
        </Section>

        <Section>
          <Label>
            <Input
              type="checkbox"
              checked={formData.newsletter}
              onChange={(e) => updateField("newsletter", e.target.checked)}
            />
            
          </Label>
        </Section>
      </Fieldset>

      <Section>
        <Button type="submit"></Button>
        <Button
          type="button"
          onClick={() => {
            setFormData({
              name: "",
              email: "",
              age: 25,
              bio: "",
              country: "",
              interests: [],
              newsletter: false,
              rating: 5,
              favoriteColor: "#000000",
            });
            setSubmitResult("");
          }}
        >
          
        </Button>
      </Section>
    </Form>
  );

  return (
    <Article>
      <Section>
        <H1> Form Elements Catalog</H1>
        <P>Tanuki UI</P>
      </Section>

      <Section>
        <H2> </H2>
        <P>Tanuki UI</P>

        {renderInteractiveForm()}

        {submitResult && (
          <Section>
            <H3> </H3>
            <pre
              style={{
                background: "#f5f5f5",
                padding: "16px",
                borderRadius: "4px",
                overflow: "auto",
                fontSize: "14px",
              }}
            >
              {submitResult}
            </pre>
          </Section>
        )}
      </Section>

      <Section>
        <H2> </H2>
        <P> ({formComponents.length}):</P>

        <Section>
          {formComponents.map((component) => (
            <Section key={component.name}>
              <H3>{component.name}</H3>
              <P>{component.description}</P>
              <Section>
                <>{component.examples.basic}</>
              </Section>
              {component.examples.variations && (
                <Section>
                  <H4>:</H4>
                  {component.examples.variations.map((variation, index) => (
                    <Section key={index}>
                      <>{variation}</>
                    </Section>
                  ))}
                </Section>
              )}
            </Section>
          ))}
        </Section>
      </Section>

      <Section>
        <H2> </H2>

        <Section>
          <H3>1. HTML</H3>
          <P>Tanuki UIHTML</P>
        </Section>

        <Section>
          <H3>2. </H3>
          <P>LabelhtmlFor</P>
        </Section>

        <Section>
          <H3>3. </H3>
          <P>HTML5requiredtypeminmax</P>
        </Section>

        <Section>
          <H3>4. </H3>
          <P>HTMLJavaScript</P>
        </Section>
      </Section>

      <Section>
        <H2> </H2>
        <P>
          
          CSS
        </P>
      </Section>
    </Article>
  );
};

export default FormCatalog;
